<template>
    <div class="login-wrap">
    	<transition name="fade">
    		<img class="login_bg"  v-if='bgshow'  :src="loginbg" /><!--:class="bgstyle==1?'loginbgstyle':''"-->
    	</transition>
        <div class="ms-title">中国华融</div>
        <div class="ms-login">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px" class="demo-ruleForm">
                <el-form-item prop="username">
                    <el-input v-model="ruleForm.username" placeholder="username"></el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input type="text"  placeholder="password" v-model="ruleForm.password" @keyup.enter.native="submitForm()"></el-input>
                </el-form-item>
                <div class="login-btn">
                    <el-button type="primary" @click="submitForm()">登录</el-button>
                </div>
                <!--<p style="font-size:12px;line-height:30px;color:#999;">Tips : 用户名和密码随便填。</p>-->
            </el-form>
        </div>
    </div>
</template>

<script>
	var bg1=require("../../assets/img/login/1.jpg");
	var bg2=require("../../assets/img/login/2.jpg");
	var bg3=require("../../assets/img/login/3.jpg");
	var bg4=require("../../assets/img/login/4.jpg");
	var bg5=require("../../assets/img/login/5.jpg");
	
    export default {
        data: function(){
            return {
                ruleForm: {
                    username: '',
                    password: ''
                },
                loginbg:'',
                bgstyle:'1',
                bgshow:true,
                rules: {
                    username: [
                        { required: true, message: '请输入用户名', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
        	login_bg(){
        		var n=0;
        		this.loginbg=bg1;
        		
        		var j = setInterval(function(){
        			this.bgshow=false;
 					n++;
 					if(n==1){
        				setTimeout(function(){
        					this.bgshow=true;
        					this.loginbg=bg1;
        				}.bind(this),10)
 					}else if(n==2){
        				setTimeout(function(){
        					this.bgshow=true;
        					this.loginbg=bg2;
        				}.bind(this),10)
        				
 					}else if(n==3){
        				setTimeout(function(){
        					this.bgshow=true;
        					this.loginbg=bg3;
        				}.bind(this),10)
 					}else if(n==4){
        				setTimeout(function(){
        					this.bgshow=true;
        					this.loginbg=bg4;
        				}.bind(this),10)
 					}else if(n==5){
        				setTimeout(function(){
        					this.bgshow=true;
        					this.loginbg=bg5;
        				}.bind(this),10)
 					}else if(n>5){
 						n=0
        				setTimeout(function(){
        					this.bgshow=true;
        					this.loginbg=bg1;
        				}.bind(this),10)
 					}
    			}.bind(this),3000)

        	},
            submitForm(formName) {
            	var name = this.ruleForm.username;  
                var password = this.ruleForm.password;  
                if(name == '' || password == ''){  
                    this.$message({  
                        message : '账号或密码为空！',  
                        type : 'error'  
                    })  
                    return;  
                }
                //发送请求
//              this.$http.get('/servlet/FINERPServlet',{
                this.$http.get('/static/data.json',{
			    	params:{
			    		"username":name,
						"userpassword":password
			    	}
		    	}).then(function (response) {
					console.log(response);
					//返回数据成功跳转到首页
					if(response.data.sucess==1){
						this.$router.push({
				  			name:'/Index',
				  			params:{
				  				'userid':response.data.userid
				  			}
				  		});
					}else if(response==2){
						this.$message({  
	                        message : '账号或密码不正确！',  
	                        type : 'error'  
	                    })  
					}else if(response==3){
						
					}
					
//					console.log(this.$route.params)调到首页可以把里面的值打印出来
					
			   	}.bind(this)).catch(function (error1) {
			         this.$message.error('请求数据失败');
			    }.bind(this));
            }
        },
        mounted(){
        	this.login_bg();
        }
    }
</script>

<style scoped>
	body {
		background: red;
	}
    .login-wrap{
        position: absolute;
        width:100%;
        height:100%;
        /*background: url(../../assets/img/login/1.jpg) no-repeat;*/
    }
    
    .fade-enter-active  {
	  transition: opacity 1s
	}
	.fade-leave-active {
		transition:0.1s
	}
	/*.fade-enter, .fade-leave-active {
	 	transition: opacity 1s
	}*/
	
    .login_bg {
    	width: 100%;
    	height: 100%;
    	/*-moz-opacity:0;  
      -khtml-opacity: 0;  
      opacity: 0;  */
    }
    /*.loginbgstyle{
    	transition: .5s;
    	-moz-opacity:1;  
      -khtml-opacity: 1; 
      opacity: 1;  
    }*/
    .ms-title{
        position: absolute;
        top:50px;
        width:100%;
        text-align: center;
        font-size:30px;
        color: #fff;
    }
    .ms-login{
        position: absolute;
        left:50%;
        top:50%;
        width:300px;
        height:160px;
        margin:-150px 0 0 -190px;
        padding:40px;
        border-radius: 5px;
        background: #fff;
    }
    .login-btn{
        text-align: center;
    }
    .login-btn button{
        width:100%;
        height:36px;
    }
</style>


