<template>
	<div class="index">
		<!--header begin -->
		<header class="clearfix">
			<div id="headerlogo">
				<img src="../../assets/img/login-logo.png"/>
			</div>
			<div class="headerdesgiter clearfix">
				<!--search-->
				<div style="float: left;">
					<el-input 
					placeholder=""
					icon="search"
					v-model="input2"
					:on-icon-click="handleIconClick">
					</el-input>
				</div>

				<!--message-->
				<div style="float: left;width: 60px;line-height: 51px;text-align: center;cursor: pointer;">
					<el-col>
						<el-dropdown trigger="click">
							<span class="el-dropdown-link" style="position: relative;">
								<i class="icon-message"></i>
								<span id="messagenumber">12</span>
							</span>
							<el-dropdown-menu slot="dropdown">
								<el-dropdown-item>更新中敬请期待。。。</el-dropdown-item> 
							</el-dropdown-menu>
						</el-dropdown>
					</el-col>
				</div>
				<div style="float: left;width: 90px;line-height: 51px;text-align: center;cursor: pointer;">
					<el-col>
						<el-dropdown trigger="click"> 
							<span class="el-dropdown-link">
								<img class="userlogo" src="../../assets/img/user.jpg"/>
								用户A
								<!--<i class="el-icon-arrow-down "></i>-->
							</span>
							<el-dropdown-menu slot="dropdown">
								<el-dropdown-item>个人信息</el-dropdown-item>
								<el-dropdown-item>账户设置</el-dropdown-item>
								<el-dropdown-item>退出登录</el-dropdown-item>
							</el-dropdown-menu>
						</el-dropdown>
					</el-col>
				</div>
			</div>
		</header>
		<!--header end -->
		<!--content begin -->
		<div class="ygcontent" style='position: relative;'> 
			<div class="L_navbar" :class="navwidth==true?'L_navwidth':''">
				<div id="navoff" @click='navoff()' >
					<i class="iconfont icon-daohang"></i>
				</div>

				<ul id="navcontent">
					<li style="position: relative;" v-for='(val,index) in navdata' @mouseover='navshowover(index)' @mouseout='navshowout()'>
						<span :class="val.icon"></span>
						<span v-show='navwidth==true'>{{val.title}}</span>
						<div v-show='navshow==index' style="width: 100px;background: #000000;color: gainsboro;position: absolute;top: 0px;right: -100px;">
							<router-link to='/Index'>
								<div class="navcontenthovertop" v-if='val.title=="首页"'>{{val.title}}</div>
							</router-link>
							<div class="navcontenthovertop" v-if='val.title!="首页"'>{{val.title}}</div>
							<div class="navcontenthover" v-if='val.title!="首页"' v-for='( val1, index1) in val.content' @click='navpath(index,index1)'>
								<router-link :to="val1.url">{{val1.title}}</router-link>
							</div>
						</div>
					</li>
				</ul>
			</div> 

			<div class="ygcontentshow" :class='{ygcontentmargin:navwidth}'>
				<router-view></router-view>
			</div>  	
		</div>
		<!--content end -->
	</div>
</template>

<script>

//	var store=require('store')
export default {
  name: 'index',
  data () {
    return {
    	//Header
    	input2:'',
    	//Sidder
    	navwidth:false,
		navshow:'-100',
		navdata:""
    }
  },
  methods:{
	  //header
  	handleIconClick(ev) {
      console.log(ev);
   	},
  	//Sidder
  	navoff(){
			this.navwidth=!this.navwidth;
		},
		navshowover(index){
			this.navshow=index
		},
		navshowout(){
			this.navshow='-100'
		},
		navpath(index,index1){
			console.log(this.navdata[index].content[index1])
		},
		calcHeight() { //设置页面最小高度 
			var _h = document.documentElement ? document.documentElement.clientHeight : document.body.clientHeight;
			_h = _h - document.querySelector('header').clientHeight; 
			document.querySelector('.L_navbar').style.minHeight = _h + 'px'; 
			
		} 
  },
  mounted(){
  	this.calcHeight();
  },
  created(){
  	console.log(this.$route.params)//获取登录过来传送过来的userid
  	
  	this.$http.get("/static/data.json").then(function(res){
			console.log(res.data)
			this.navdata=res.data.navdata;
			
		}.bind(this)).catch(function(res){
			
		}.bind(this))
		

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="scoped">
</style>



