<template>
	<div class="y-dialog">
		 <el-dialog title="工作履历" :show-close="showClose" :visible="VisibleFlag">
			<el-form :model="myForm" ref="myForm" label-width="100px" class="demo-ruleForm">
				<el-form-item label="起止时间" prop="date" :label-width="formLabelWidth" 
					:rules="[{ required: true, message: '日期不能为空'}]">
					<el-date-picker
						v-model="myForm.date"
						type="daterange"
						placeholder="选择日期范围">
					</el-date-picker>  
				</el-form-item>
				<el-form-item label="工作单位" prop="danwei" :label-width="formLabelWidth" 
					:rules="[{ required: true, message: '工作单位不能为空'}]">
					<el-input type="age" v-model="myForm.danwei"></el-input>
				</el-form-item> 
				<el-form-item label="担任职务" :label-width="formLabelWidth">
					<el-input v-model="myForm.zhiwu"></el-input>  
				</el-form-item> 
				<el-form-item label="主要职责" :label-width="formLabelWidth">
					<el-input v-model="myForm.zhize"></el-input>  
				</el-form-item> 
				<el-form-item :label-width="formLabelWidth">
					<el-button type="primary" @click="submitForm('myForm')">确定</el-button>
					<el-button @click="cancle()">取消</el-button>
				</el-form-item>
			</el-form> 
		</el-dialog>
	</div>
</template>
<script>
export default {
	props: ['VisibleFlag'],  
	data() {
		return {  
			showClose: false, //隐藏右上角关闭按钮
			myForm: { 
				date: '',
				danwei: '',
				zhiwu: '',
				zhize: '' 
			},
			formLabelWidth: '120px' 
		}
	},  
	methods: {  
		//确定操作
		 submitForm(){ 
		 	this.$refs['myForm'].validate((valid) => { 
				if (valid) {  
					this.$emit('submit', this.myForm );  
					this.resetForm();
				} else {
					return false;
				};
	        });
		 	
		 },
		 //取消操作
		 cancle() { 
	        this.resetForm();
	        this.$emit('close');
	     },
	     resetForm(){
	     	for(var i in this.myForm ){
	     		this.myForm[i] = '';
	     	};
	     	this.$refs['myForm'].resetFields();
	     } 
	}
}
</script>
 


