import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/components/login/login';//登录引用
import Index from '@/components/main/index';//公共部分
import homepage from '@/components/main/homepage';//主页内容
//我的信息部分
import Information from '@/components/query/myinformation/information';
//业务办理部分
import Fullapplicationlist from '@/components/Business/Fullapplication/Fullapplicationlist';
import Fullapplicationadd from '@/components/Business/Fullapplication/Fullapplicationadd';
import Fullapplicationdetails from '@/components/Business/Fullapplication/Fullapplicationdetails';

Vue.use(Router)

export default new Router({
  routes: [
  	{
  	  path: '/',
      name: '/Login',
      /*component: Login*/
      component: Index,
  	},
    {
      path: '/Index',
      name: '/Index',
      component: Index,
		  children: [  
		    { 
		    	path: "/",
		    	name: '/homepage',
		    	component: homepage 
		    },
		    {
		    	path: "/Information",
		    	name: '/Information',
		    	component: Information 
		    },
		    {
		    	path: "/Fullapplicationlist",
		    	name: '/Fullapplicationlist',
		    	component: Fullapplicationlist 
		    },
		    {
		    	path: "/Fullapplicationadd",
		    	name: '/Fullapplicationadd',
		    	component: Fullapplicationadd 
		    }
		    ,
		    {
		    	path: "/Fullapplicationdetails",
		    	name: '/Fullapplicationdetails',
		    	component: Fullapplicationdetails 
		    }
		    
		  ]  
    }
    
  ]
})



