<template>
	<div>
		<div class="y-breadcrumb-box">
			<el-breadcrumb separator="/"> 
			  <el-breadcrumb-item :to="{ path: '/Index' }">首页</el-breadcrumb-item>
			  <el-breadcrumb-item>人事管理</el-breadcrumb-item> 
			  <el-breadcrumb-item>我的信息</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="y-row-box">
			<div class="y-inner">  
				<!-- 编辑基本信息 begin -->
				<el-form class="y-module" ref="form" :model="form" label-width="80px" label-position="left">  
					<div class="y-title">
						<span class="y-right" :class="EditBaseInfo ? 'y-hide' : 'y-show'"> 
							<el-button @click="EditBaseInfo = true" type="primary" size="small">编辑</el-button> 
						</span>
						<span class="y-right" :class="EditBaseInfo ? 'y-show' : 'y-hide'"> 
							<el-button @click="saveBaseInfo" type="primary" size="small">保存</el-button>
							<el-button @click="EditBaseInfo = false" type="primary" size="small">取消</el-button>
						</span>
						<i class="iconfont icon-mycenter text-yellow"></i>  
						基本信息 
					</div>
					<el-row :gutter="5">
						<el-col :xs="24" :sm="12" :md="8" :lg="6" v-for="item in initdata">
							<el-form-item :label="item.text"> 
								<el-input v-if="!item.edit" readonly="readonly" :value="item.val"></el-input>
								<el-input v-else :readonly="!EditBaseInfo" v-model="form[item.id]"></el-input>
							</el-form-item>
						</el-col> 
					</el-row>  
				</el-form> 
				<!-- 编辑基本信息 end -->
				<!-- 编辑工作履历 begin -->
				<div class="y-module">
					<div class="y-title">
						<span class="y-right" :class="EditJobInfo ? 'y-hide' : 'y-show'"> 
							<el-button @click="EditJobInfo = true" type="primary" size="small">编辑</el-button> 
						</span>
						<span class="y-right" :class="EditJobInfo ? 'y-show' : 'y-hide'"> 
							<el-button @click="saveJobInfo" type="primary" size="small">保存</el-button>
							<el-button @click="EditJobInfo = false" type="primary" size="small">取消</el-button>
						</span>
						<i class="iconfont icon-mycenter text-yellow"></i>  
						工作履历
					</div>
					<el-table :data="tableData" style="width: 100%">
						<el-table-column :render-header="requiredCol" prop="date" label="起止时间">
						</el-table-column>
						<el-table-column :render-header="requiredCol" prop="danwei" label="工作单位">
						</el-table-column>
						<el-table-column prop="zhiwu" label="担任职务">
						</el-table-column>
						<el-table-column prop="zhize" label="主要职责">
						</el-table-column>
						<el-table-column>
							<template scope="scope"> 
								<el-button
								size="small"
								type="danger"
								@click="deleteJobInfo(scope.$index, scope.row)">删除</el-button>
							</template>
						</el-table-column>
					</el-table>
					<div class="text-center" :class="EditJobInfo ? 'y-show' : 'y-hide'">
						<span class="text-blue" @click="ShowAddJob = true">添加工作履历</span>
					</div>
				</div> 
				<!-- 编辑工作履历 end -->
			</div> 
		</div>
		<AddJob :VisibleFlag="ShowAddJob" @close="ShowAddJob = false" @submit="addJobInfo"></AddJob>  
	</div>
</template>
<script>
//添加工作经历
import AddJob from './components/addJob';
import {formatDate} from '@/assets/js/date.js';
export default {
	data() {
		return {  
	        EditBaseInfo: false, //编辑基本信息
	        EditJobInfo: false, //编辑工作履历
	        ShowAddJob: false, //添加工作履历
			initdata: [{
				id: 'name',
				text: '姓名',
				val: '张三',
				edit: false
			},{
				id: 'sex',
				text: '性别',
				val: '男',
				edit: false
			},{
				id: 'date',
				text: '出生日期',
				val: '1990-6-18',
				edit: false
			},{
				id: 'mail',
				text: '电子邮箱',
				val: 'zhangsan@yonyou.com',
				edit: true
			},{
				id: 'phone',
				text: '手机',
				val: '187xxxxxxxx',
				edit: true
			}],   
	        tableData: [{
				date: '2016-05-02',
				danwei: '单位1',
				zhiwu: '经理1',
				zhize: 'aa'
			}, {
				date: '2016-05-04',
				danwei: '单位2',
				zhiwu: '经理',
				zhize: 'bb'
			}]
		}
	},
	components: {
		AddJob //添加工作履历弹窗
	},
	computed: { 
		// 计算属性
		form: function () { 
			var initdata = this.initdata;
			var _form = {};
			for(var i = 0, l = initdata.length; i < l; i++){
				var _d = initdata[i];
				var _id = _d.id;

				if( _d.edit ){
					_form[_id] = _d.val;
				};
			}; 
			return _form;
		}
	},
	methods: { 
		//基本信息-[保存]
		saveBaseInfo(){ 
			console.info( 'saveBaseInfo基本信息：',this.form ); 
			this.EditBaseInfo = false;
		},
		//工作履历-[动态添加]
		addJobInfo( data ){
			var _sdate = formatDate( data.date[0], 'yyyy-MM-dd');
			var _edate = formatDate(data.date[1], 'yyyy-MM-dd'); 
			data.date = _sdate + '至' + _edate;   

			this.ShowAddJob = false;   
			this.tableData.push( JSON.parse(JSON.stringify( data )) ); 
		},
		//工作履历-[动态删除]
		deleteJobInfo(index, row) { 
			this.tableData.splice(index, 1);
		},
		//工作履历-[保存]
		saveJobInfo(){

		}, 
		//列表必填列
		requiredCol(createElement, { column }) {
			return [ 
				createElement('span', {
					'class': 'text-red'
				},'*'), column.label 
			];
		} 
	}
}
</script> 


